html, body {
  background-color: #000;
  margin: 0;
  padding: 0;
}

h3 {
  margin: 0;
}

.menu button {
  all: unset;
  cursor: pointer;
  background-color: gray;
  border-radius: 3px;
  font-family: silkscreen;
  box-shadow: 0 4px #000;
}

.menu button:focus-visible, .menu button:hover {
  outline: 1px solid #fff;
}

.menu button:active {
  transform: translate(0, 4px);
  box-shadow: 0 0 #000;
}

#game {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.excalibur-scale {
  transform-origin: 0 0;
  transform: scale(var(--pixel-conversion), var(--pixel-conversion));
}

.menu {
  left: var(--pointer-x);
  top: var(--pointer-y);
  color: #fff;
  background-color: #0007;
  border-radius: 5px;
  flex-direction: column;
  width: 100px;
  padding: 5px;
  font-family: silkscreen;
  display: flex;
  position: absolute;
}

.menu button {
  margin-bottom: 10px;
}

.hide {
  display: none;
}

.show {
  display: block;
}
